import Axios from 'axios';
import Swal from 'sweetalert2';
import {
  router
} from "../../routes";

//   .
// ?    .
// state
const state = {
  user: {},
};

//  vuex 
//   .
// dispatch
const actions = {
  logout({
    commit
  }) {
    const jwt = localStorage.getItem('routrip_JWT');
    //vuex user 
    Axios.post('http://52.78.8.59:8083/account/logout/', {
      jwt: jwt
    }).then(() => {
      commit('setUser', null);
      Swal.fire({
        icon: 'warning',
        title: '',
        text: ' !',
      }).then(() => {
        router.push("/");
      })

      // 
      localStorage.clear();
    }).then(() => {
      router.push('/');
    }).catch(error => {
      console.log(error.response.status);
      if (error.response.status === 406) {
        Swal.fire({
          icon: 'error',
          title: ' ',
          text: '  !',
          confirmButtonText: 'Login',
        }).then(() => {
          router.push("/");
        })
        console.log("logout 406 ERROR");
      }
    });
  },
  async reqUserInfo({
    commit
  }) {
    const jwt = localStorage.getItem('routrip_JWT');
    await Axios.post('http://52.78.8.59:8083/account/decode/', {
      jwt: jwt
    }).then(res => {
      commit('setUser', res);
    }).catch(error => {
      if (error.response.status === 406) {
        console.log("reqUserInfo 406 ERROR");
      }
    });
  },

};

//  state   
//  .
// getters
const getters = {
  getUser: state => state.user,
};

//  state   
//  .
// commit
const mutations = {
  setUser: (state, user) => (state.user = user),
};

//     .
export default {
  namespaced: true,
  state,
  actions,
  getters,
  mutations
};