<template>
    <div id="change-password" class="wrapC">
        <h1> </h1>
        <h3>   .</h3>

        <div class="input-password">
            <div class="input-with-label">
                <input
                    v-model="password"
                    v-bind:class="{ error: error.password, complete: !error.password && this.password.length !== 0 }"
                    @keyup.enter="password"
                    id="password1"
                    placeholder=" ."
                    type="password"
                />
                <label for="password1"></label>
                <div class="error-text" v-if="error.password">{{ error.password }}</div>
            </div>
            <div class="input-with-label">
                <input
                    v-model="password2"
                    v-bind:class="{ error: error.password2, complete: !error.password2 && this.password2.length !== 0 }"
                    @keyup.enter="password2"
                    id="password2"
                    placeholder=" ."
                    type="password"
                />
                <label for="password2"> </label>
                <div class="error-text" v-if="error.password2">{{ error.password2 }}</div>
            </div>
            <div class="password-error-msg" :class="{ samePassword: !samePassword }">
                <span style="color:red;">  .</span>
            </div>
        </div>

        <button
            @click="register"
            class="btn btn--back"
            :disabled="!registerBtn"
            :class="{ disabled: !registerBtn }"
        ></button>
        <div class="back" @click="toLogin"></div>
    </div>
</template>

<script>
import '../../assets/css/style.scss';
import '../../assets/css/user.scss';
import PV from 'password-validator';
import UserApi from '../../apis/UserApi';
import Swal from 'sweetalert2';
export default {
    created() {
        this.passwordSchema
            .is()
            .min(8)
            .is()
            .max(100)
            .has()
            .digits()
            .has()
            .letters();
    },
    watch: {
        password: function(v) {
            this.checkPassword1();
        },
        password2: function(v) {
            this.checkPassword2();
        },
    },
    methods: {
        checkPassword1() {
            if (this.password.length >= 0 && !this.passwordSchema.validate(this.password))
                this.error.password1 = ',  8  .';
            else this.error.password1 = false;
        },
        checkPassword2() {
            if (this.password2.length >= 0 && !this.passwordSchema.validate(this.password2)) {
                this.error.password2 = ',  8  .';
                this.registerBtn = false;
            } else {
                this.error.password2 = false;

                if (this.password !== this.password2) {
                    this.samePassword = true;
                    this.registerBtn = false;
                } else {
                    this.samePassword = false;
                    this.registerBtn = true;
                }
            }
        },
        register() {
            if (this.registerBtn) {
                var { password } = this;
                var email = localStorage.getItem('authEmail');
                var data = { email, password };
                UserApi.passwordReset(
                    data,
                    res => {
                        Swal.fire({
                            icon: 'success',
                            title: '  ',
                            text: ' .',
                        });
                        this.$emit('resetPassword');
                    },
                    error => {
                        // console.log(error);
                        this.$router.push('/error');
                    },
                );
            }
        },
        toLogin() {
            let flag = confirm('  ?');
            if (flag) this.$router.push('/');
        },
    },
    data: () => {
        return {
            passwordSchema: new PV(),
            password: '',
            password2: '',
            error: {
                password: false,
                password2: false,
            },
            isSubmit: false,
            samePassword: false,
            registerBtn: false,
        };
    },
};
</script>
