<template>
    <div class="profile-page">
        <Header></Header>
        <div class="wrapD">
            <h2>My Profile</h2>
            <div class="profile-wrap">
                <UserPicture :userPicture="true" :pic="userinfo.pic" />
                <div class="user-info">
                    <HeaderComponent :headerTitle="userinfo.email" :profileIcon="false" :mailIcon="true" />
                    <HeaderComponent :headerTitle="userinfo.nickname" :profileIcon="true" rightText="" @changeNick="changeNick" />
                <div class="none-border">
                    <button class="button-text" @click="delUser"></button>
                </div>
                </div>
            </div>
            <div class="wrap">
                <router-link v-bind:to="{ name: 'UserPost' }"><TabComponent tabTitle=" " :isActive="true"/></router-link>
                <router-link v-bind:to="{ name: 'UserComment' }"><TabComponent tabTitle="" :isActive="true"/></router-link>
                <router-link v-bind:to="{ name: 'UserLike' }"><TabComponent tabTitle="" :isActive="true"/></router-link>
                <router-link v-bind:to="{ name: 'UserPeople' }"><TabComponent tabTitle="" :isActive="true"/></router-link>
                <router-link v-bind:to="{ name: 'UserScrap' }"><TabComponent tabTitle="" :isActive="true"/></router-link>
            </div>

            <div class="profile-tab-page">
                <router-view name="profile"></router-view>
            </div>
        </div>
    </div>
</template>


<script>
import Axios from 'axios';
import Swal from 'sweetalert2';
import { createNamespacedHelpers } from 'vuex';

import HeaderComponent from '../../../components/common/Header';
import UserPicture from '../../../components/common/UserPicture';
import TabComponent from '../../../components/common/Tab';
import Header from '../../jh/Header';

import '../../../assets/css/profile.scss';
import '../../../assets/css/style.scss';

const userMapActions = createNamespacedHelpers('User').mapActions; //
const userMapGetters = createNamespacedHelpers('User').mapGetters; //

export default {
    components: {
        HeaderComponent,
        UserPicture,
        TabComponent,
        Header,
        
    },
    computed: {
        ...userMapGetters(['getUser']),
    },
    mounted() {
        this.getInfo();
        this.checkLogin();
        this.reqInfo();
    },
    methods: {
        ...userMapActions(['reqUserInfo']),
        ...userMapActions(['logout']),

        async reqInfo() {
            await this.reqUserInfo();
            this.userinfo.nickname=this.getUser.data.nickname;
            this.userinfo.pic=this.getUser.data.profileImg;
            this.userinfo.email=this.getUser.data.email;
            if(!this.getUser.data.email){
                this.userinfo.email='SNS'
            }
        },

        async delUser(){
            await Swal.fire({
            title: '  .',
            text: "  .( )",
            input: 'password',
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#d33',
            inputPlaceholder: 'SNS   .',
            inputValue: '',
            inputValidator:(value)=>{
            if (!value) {
              return '  .'
            }
            else{
                const jwt = localStorage.getItem('routrip_JWT');
                Axios.delete('http://52.78.8.59:8083/account/user/',
                        {   data:{
                            jwt: jwt,
                            password: value}
                        }
                    ).then(res => {
                        // console.log(res.headers);
                        // console.log(jwt)
                        // console.log('',res)
                            Swal.fire({
                            icon:"success",
                            title:'  . \n   !'
                        }).then( (dismiss)=>{
                            if (dismiss){
                                
                                this.$router.push('/');
                            }
                        }
                    )       
                    }).catch(error=>{
                       Swal.fire({
                           title:' ,   .',
                           icon:'error'
                       })
                    });
            }}})
                        
        },

    getInfo() {
      this.userinfo.email = localStorage.getItem('loginedEmail');
      this.userinfo.nickname = localStorage.getItem('nickName');
    },
    checkLogin() {
      if (localStorage.getItem('loginedEmail') !== null) {
        this.show = true;
      } else {
        this.show = false;
      }
    },

    async changeNick() {
      await Swal.fire({
        title: '  .',
        input: 'text',
        inputValue: this.userinfo.nickname,
        showCancelButton: true,
        inputValidator: value => {
          if (!value) {
            return ' !';
          } else {
            const jwt = localStorage.getItem('routrip_JWT');
            Axios.put('http://52.78.8.59:8083/account/user/', {
              nickname: value,
              jwt: jwt,
            }).then(res => {
              // console.log(res.data);
              localStorage.setItem('routrip_JWT', res.data);
              this.reqInfo();
            });
          }
        },
      });
    },
  },
  data() {
        return {
            hi:'',
            userinfo: {
                email: '',
                nickname: '',
                pic:'',
            },
            show: false,
        };
    },
};
</script>
