import Vue from 'vue';
import Vuex from 'vuex';

// 1.  vuex Javascript  import .
import UserStore from './user/userStore';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        flag: false,
    },
    getters: {
        getLoading: state => state.flag,
    },
    mutations: {
        setLoading: (state) => (state.flag = !state.flag),
    },
    actions: {},
    // 2. modules .
    modules: {
        User: UserStore,
    },
});
